@for $i from 1 through 12 {
  @if ($i == 12) {
    .col-full {
      grid-column: 1 / -1;
    }
  } @else {
    .col-#{$i} {
      grid-column-end: span $i;
    }
    .row-#{$i} {
      grid-row-end: span $i;
    }
  }
}

.grid {
  display: grid !important;
  grid-template-columns: repeat(12, 1fr);
}

.gap-1 {
  gap: 0.25rem;
}
.gap-2 {
  gap: 0.5rem;
}
.gap-3 {
  gap: 0.75rem;
}
.gap-4 {
  gap: 1rem;
}
.gap-5 {
  gap: 1.25rem;
}
.gap-6 {
  gap: 1.5rem;
}
.gap-7 {
  gap: 1.75rem;
}
.gap-8 {
  gap: 2rem;
}
.gap-9 {
  gap: 2.25rem;
}
.gap-10 {
  gap: 2.5rem;
}
